// import nested from './nested'

// initial state
// shape: [{ id, quantity }]
const state = () => ({
	dataSource: [],
})

// getters store 的计算属性
const getters = {
	
}

// actions 给 store 赋值 异步mutations
const actions = {
	/*
	  通过commit(key<String>, value<Any>）调用mutations内的方法
	**/
	async checkout({ commit, state }, e) {
		// empty dataSource
		commit('changeSource', [])
		try {
			let val = await asyncTime(e)
			commit('changeSource', ...val)
		} catch (e) {
			console.error(e)
		}
	},
}

// mutations 给 store 赋值
const mutations = {
	/*
		一般内部的方法修改state的值
	**/
	changeSource(state, val) {
		state.dataSource = val
	},
}

// 随便一个计时器的方法（模拟请求接口）
async function asyncTime(e) {
	// await wait(100)
	if (!!e) {
		return [e]
	} else {
		throw new Error('Checkout error')
	}
}

export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations,
	modules: {
		// nested
	}
}