					<keep-alive>
						<div>
							<component :is="Component" :key="route.meta.usePathKey ? route.path : undefined"/>
						</div>
					</keep-alive>
				</transition>
			</router-view>
		</div>
	</el-config-provider>
</template>

<script>
import '@/assets/font/font.css';
import { ElConfigProvider } from 'element-plus'
import zhCn from 'element-plus/lib/locale/lang/zh-cn'
export default {
	name: "App",
	components: {
		ElConfigProvider,
	},
	data() {
		return {
			locale: zhCn,
		}
	},
	created() {

	},
	mounted() {

	},
	methods: {

	}
};
