<style lang="less">
* {
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	::-webkit-scrollbar {
		width: 0;
	};
}
body {
	overflow: hidden;
}
input {
	border: 0;
